<template>
	<view class="container mms-page wechat-page">
		<view class="search-area">
			<view class="search-icon"  @click="showDrawer"><uni-icons custom-prefix="iconfont" color="#ffffff" type="icon-sousuo" size="24"></uni-icons></view>
			<view class="search-info">
				<view class="icon-scan" @click="onScan">
					<uni-icons custom-prefix="iconfont" color="#ffffff" type="icon-saoyisao" size="28"></uni-icons>
				</view>
			</view>
		</view>
		
		<view class="mms-content section">
		    <uni-forms :modelValue="formData" v-if="updateData">
		            <uni-forms-item required name="hobby" label="物料编号">
						<uni-easyinput disabled type="text" v-model="formData.materialsNo" placeholder="物料编号" />
					</uni-forms-item>
		            <uni-forms-item required name="hobby" label="物料名称">
						<uni-easyinput disabled type="text" v-model="formData.materialsName" placeholder="物料名称" />
		            </uni-forms-item>
					<uni-forms-item required name="hobby" label="规格型号">
					   <uni-easyinput disabled type="text" v-model="formData.materialsModel" placeholder="规格型号" />
					</uni-forms-item>
		            <uni-forms-item required name="hobby" label="物料单位">
		               <uni-easyinput disabled type="text" v-model="formData.materialsUnit" placeholder="物料单位" />
		            </uni-forms-item>
		            <uni-forms-item required name="hobby" label="入库数量">
		               <uni-easyinput type="number" v-model="formData.wareHousingNum" placeholder="物料数量" />
		            </uni-forms-item>
					<uni-forms-item required name="hobby" label="存放位置">
						<view class="input-style" @click="onOpenPickerview">
						  {{formData.storehouseName}}
						  <view class="input-arrow"><uni-icons custom-prefix="iconfont" color="#888" type="icon-right" size="16"></uni-icons></view>
						</view>
					</uni-forms-item>
		            <uni-forms-item name="hobby" label="所属部门">
		               <uni-easyinput type="text" v-model="formData.dept" placeholder="所属部门" />
		            </uni-forms-item> 
		            <uni-forms-item name="hobby" label="操作员">
		               <uni-easyinput disabled type="text" v-model="formData.operator" placeholder="操作员" />
		            </uni-forms-item>
					<uni-forms-item name="hobby" label="备注">
					   <uni-easyinput type="text" v-model="formData.remark" placeholder="备注" />
					</uni-forms-item>
		            <uni-forms-item label="入库日期" name="time">
						<uni-datetime-picker
						    type="date"
						    :value="wareHousingTime"
						    :end="endTime"
						    @change="onTimeChange"
						/>
		            </uni-forms-item>
					<uni-forms-item>
						 <button class="mms-btn" @click="setInWarehouse">添加入库单</button>
					</uni-forms-item>
		        </uni-forms>
			</view>
			
			<view class="picker-box" v-if="pickerViewShow">
				<view class="picker-box-title"><view class="picker-title-close" @click="onClosePicker">确定</view></view>
				<picker-view  :value="formData.storehouseId" @change="onStorehoseChange" class="picker-view">
					<picker-view-column>
						<view class="item" v-for="(item,index) in storeHouseList" :key="index">{{item.storehouseName}}</view>
					</picker-view-column>
				</picker-view>  
			</view>
			
			<uni-drawer ref="showRight" mode="right" width="280" :mask-click="false">
				<view class="mms-drawer-body">
					<view class="mms-close" @click="closeDrawer" type="primary">x</view>
					<view class="mms-drawer-search">
						<uni-forms :modelValue="searchData">
						        <uni-forms-item name="hobby" label="物料名称">
						            <uni-easyinput type="text" v-model="searchData.materialsName" placeholder="物料名称" />
						        </uni-forms-item>
						        <uni-forms-item name="hobby" label="物料编号">
						            <uni-easyinput type="text" v-model="searchData.materialsNo" placeholder="物料编号" />
						        </uni-forms-item>
								<uni-forms-item name="hobby" label="规格型号">
								    <uni-easyinput type="text" v-model="searchData.materialsModel" placeholder="规格型号" />
								</uni-forms-item>
						</uni-forms>
						<button class="mms-btn" @click="getProList">查 询</button>
					</view>
					<view class="materlist list">
						<scroll-view style="height: 100%;" scroll-y="true">
						  <view class="list-item" v-for="item in resList" :key="item.materialsNo" @click="onCheckMater(item)">
							<view>物料名称：<view class="list-item-info">{{item.materialsName}}</view></view>
							<view>物料编号：<view class="list-item-info">{{item.materialsNo}}</view></view> 
							<view>规格型号：<view class="list-item-info">{{item.materialsModel}}</view></view>
							<view>库存数量：<view class="list-item-info">{{item.inStockNum}} {{item.materialsUnit}}</view></view>
						  </view>
						</scroll-view>
					</view>
				</view>
			</uni-drawer>
	</view>
</template>

<script>
	const util = require('../../static/js/util.js');
	export default {
		data() {
			return {
				userState: {},
				userInfo:{},
				searchData:{}, 
				resList:[], 
				formData:{
					storehouseName:''
				},
				storehouseCheck:{},
				pickerViewShow:false,
				storeHouseList:[],
				materialsNo:'',
				endTime:'',
				wareHousingTime:'',
				updateData:true,
			}
		},
		created(){
			this.userState = util.xcStorage.getStorageSync('userState');
			this.userInfo = util.xcStorage.getStorageSync('userInfo');
			this.formData.operator = this.userInfo.userName
			this.formData.dept = this.userInfo.dept.deptName;
			
			// 初始化日期
			var thisDate = this.dateFormat();
			this.wareHousingTime = thisDate;
			this.formData.wareHousingTime = thisDate;
			this.endTime = thisDate;
			this.getStoreHouseList();
		},
		methods: {
			// 根据物料编号获取物料信息
			onScan: function(_this,type) {
				if(!type || type != 1){
					var _this = this;
				}
				// 只允许通过相机扫码
				uni.scanCode({
				    onlyFromCamera: true,
				    success: function (res) {
				        let materialsNo = util.xcUrlCode.getMaterialsNo(res.result, 'code');
						_this.materialsNo = materialsNo;
						_this.getProInfo();
				    }
				});
			}, 
			// 根据查询条件获取物料信息
			getProList(){
				var _this = this;
				var ifParam = false;
				if(!_this.searchData.materialsName || _this.searchData.materialsName == ""){
					delete _this.searchData.materialsName;
				}else{
					ifParam = true;
				}
				if(!_this.searchData.materialsNo || _this.searchData.materialsNo == ""){
					delete _this.searchData.materialsNo;
				}else{
					ifParam = true;
				}
				if(!_this.searchData.materialsModel || _this.searchData.materialsModel == ""){
					delete _this.searchData.materialsModel;
				}else{
					ifParam = true;
				}
				if(!ifParam){
					uni.showToast({
						title:'输入查询条件！',
						icon:'error',
						duration:3000 
					});
					return;
				}
				
				let url = util.xcRequestURL.WAREHOUSE.getMaterialList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,this.searchData, function(res) {
					if(res.status == 200){
						let resList = res.result;
						if(resList.length > 0){ 
						     _this.resList = res.result;
							_this.updateData = false;
							_this.updateData = true;
						}else{
							_this.resList = [];
							uni.showToast({
								title:'暂无记录！',
								duration:3000 
							});
						}
					}
				});
			},
			// 选择某个物料填入入库单
			onCheckMater(result){
				let data = this.formData;
				data.materialsModel = result.materialsModel; 
				data.materialsName = result.materialsName; 
				data.materialsUnit = result.materialsUnit; 
				data.materialsNo = result.materialsNo; 
				data.currentInStockNum = result.inStockNum;
				this.formData = data;
				this.$refs.showRight.close();
				this.updateData = false;
				this.updateData = true;
			},
			dateFormat() {
			  var data = new Date()
			  var year = data.getFullYear();
			  var month = data.getMonth() + 1;
			  var day = data.getDate();
			  // var hours = data.getHours();
			  // var minutes = data.getMinutes();
			  // var seconds = data.getSeconds();
			  
			  month = month >= 10 ? month : '0' + month;
			  day = day >= 10 ? day : '0' + day;
			  
			  var date = year + "-" + month + "-" + day;
			  return date;
			},
			// 根据物料编号获取物料信息
			getProInfo(){
				var _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getMaterialsByMaterialNo + "?token=" + _this.userState.token + '&materialsNo='+_this.materialsNo;
				util.xcRequest.xcGet(url,{}, function(res) {
					const { result } = res;
					if(res.status == 200){
						if(result !={} && result.materialsNo){
							let data = _this.formData;
							data.materialsModel = result.materialsModel; 
							data.materialsName = result.materialsName; 
							data.materialsUnit = result.materialsUnit; 
							data.materialsNo = result.materialsNo; 
							data.currentInStockNum = result.inStockNum;
							_this.formData = data;
							_this.updateData = false;
							_this.updateData = true;
						}else{
							uni.showToast({
								title: '暂无此物料信息！',
								icon: 'none',
								duration:2000 
							});
						}
						
					}
				});
			},
			// 获取物料信息位置信息列表
			getStoreHouseList(){
				var _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getStoreHouseList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{}, function(res) {
					if(res.status == 200){
						let storeHouseList = res.result;
						if(storeHouseList.length > 0){
							_this.storeHouseList = storeHouseList;
							_this.formData.storehouseName = '';
							_this.formData.storehouseName = storeHouseList[0].storehouseName;
							_this.formData.storehouseId = storeHouseList[0].id;
						}
					}
				});
			},
			// 存放位置监听
			onStorehoseChange(e){
				let val = e.detail.value;
				this.storehouseCheck.storehouseId = this.storeHouseList[val[0]].id;
				this.storehouseCheck.storehouseName = this.storeHouseList[val[0]].storehouseName;
			},
			onOpenPickerview(){ 
				uni.hideKeyboard();
				this.pickerViewShow = true;
			},
			onClosePicker(e){
				this.formData.storehouseId = this.storehouseCheck.storehouseId;
				this.formData.storehouseName = this.storehouseCheck.storehouseName;
				this.pickerViewShow = false;
			},
			// 查询弹出框
			showDrawer() {
			    this.$refs.showRight.open();
			},
			closeDrawer() {
			    this.$refs.showRight.close();
			},
			// 添加保存入库单
			setInWarehouse(){
				var _this = this;
				if(!_this.formData.materialsNo || _this.formData.materialsNo==''){
					uni.showToast({
						title: '扫二维码填入物料信息！',
						icon: 'none'
					});
					return;
				}
				if(!_this.formData.storehouseId || _this.formData.storehouseId==''){
					uni.showToast({
						title: '请选择入库的物料位置！',
						icon: 'none'
					});
					return;
				}
				if(!_this.formData.wareHousingNum || _this.formData.wareHousingNum==''){
					uni.showToast({
						title: '请输入入库数量！',
						icon: 'none'
					});
					return;
				}
				const url = util.xcRequestURL.WAREHOUSE.inWareHouse + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url, _this.formData, function(res) {
					// const { result } = res;
					if(res.status == 200){
						
						uni.showToast({
							title: '添加成功！',
							icon: 'none',
							duration:2000
						});
						uni.redirectTo({
							url: '/pages/inRecord/inRecord'
						});
						// _this.formData = {};
					}
				});
			},
			onTimeChange(e){
				this.wareHousingTime = e;
				this.formData.wareHousingTime = e;
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "../../static/css/style.css";
	.mms-page{
		box-sizing: border-box;
		width: 100%;
		word-break: break-all;
		font-size: 14px;
		line-height: 24px;
		
		.section{
			background:#fff;
		}
		
		.mms-page .mms-input .is-input-border{
			border:0;
			border-bottom:1px solid #f0f0f0;
		}
		.picker-box{
			position:fixed;
			bottom:0;
			width:100%;
			z-index:99;
			background:#fff;
			border-top:1px solid #ddd;
		}
		.picker-box-title{
			padding:8px 20px;
		}
		.picker-box .picker-view{
			height:160px;
			text-align:center;
		}
		.picker-title-close{
			width:50px;
			height:26px;
			line-height:26px;
			color:#6d3b85;
			text-align:center;
			border-radius:20px;
			border:1px solid #6d3b85;
			font-size:14px;
		}
		.input-style{
			position:relative;
			padding:0 20px 0 10px;
			color:#444;
			font-size:14px;
			min-height:30px;
			line-height:2;
			border-bottom:1px solid #ddd;
		}
		.input-arrow{
			position:absolute;
			top:0;
			right:10px;
		}
		.mms-btn{
			height:36px;
			line-height:34px;
		}
		.materlist{
			padding:10px 0 0;
			height:calc(100vh - 260px);
			line-height:1.6;
			overflow-y:auto;
			.list-item{
				font-size:13px;
				.list-item-info{
					width: calc(100% - 70px);
				}
			}
		}
	}
	    
		
</style>
